import graceNumberBox from "../../components/my-components/Number-Box.vue";
export default {
    props: {
        info: {
            type: Object,
            default: {
                name: '女神节专享通用优惠券',
                price: 20,
                info: '部分商品满100减20',
                num: 0
            }
        }
    },
    components: {
        graceNumberBox
    },
    methods: {
        change(num) {
            this.$emit('change', Object.assign(this.info, {
                num: num[0]
            }))
        }
    },
}
